import {
    Button,
    Card,
    CardBody,
    Tab,
    Tabs,
  } from "react-bootstrap";
  import { baseUrl } from "../../../../../utils";
  import React, { Suspense, useState } from "react";
  import NoData from "../../layout/common/noData";
  import ConnectButton from "../../layout/common/connectButton";
  import AuthorInfo from "./authorInfo";
  import PostMedia from "../../layout/common/postMedia";
  import PostPopup from "../../layout/popup/postPopup";
  import { getwallRecordSentimentByPostStatisticsNagativeMore, getwallRecordSentimentByPostStatisticsPositiveMore } from "../../../../../actions/analyticsNew";
  import PostdetailPopup from "../../layout/popup/postdetailPopup";
  import { useEffect } from "react";
  import SectionHeading from "../../layout/common/sectionHeading";
  import NetworkIcon from "../../layout/common/networkIcon";
import PopupModal from "../../layout/common/popup/popupModal";
import { includes } from "lodash";
  const SentimentpostSkeleton = React.lazy(() =>
    import("../../layout/skeleton/sentimentpostSkeleton")
  );
  
  const PostData = (e) => {
    return (
      <div className="px-0 py-3 cursor-pointer border-bottom">
        <div className="d-flex align-items-center flex-md-row flex-column">
          <div className="d-flex align-items-center w-100">
           {![1].includes(e.type)  ? 
            <div onClick={() => { e.setpostInfo(e); e.handlePostpopup(true) }} className="symbol_item position-relative">
              <div className="symbol symbol-100 symbol-2by2 flex-shrink-0 me-4 rounded-8">
                <Button
                  type="button"
                  className="border-0 p-0 bg-transparent rounded"
                >
                   <span
                    className="symbol-label no-repeat-fit-background rounded"
                  >

                    <PostMedia
                      mediaType={e.type }
                      postFile={e.postFile}
                      postId={"postPrimaryId"}
                      className={"img-fluid"}
                    />
                  </span>
                </Button>
              </div>
            </div> : null }
            <div className="w-100 d-flex align-items-center">
              <div className="w-100 me-md-10">
                <div className="d-flex flex-grow-1 w-100 mb-2 justify-content-between pe-md-10">
                  <AuthorInfo
                    picture={e.authorImage}
                    authorUsername={e.authorUsername}
                    date={e.created}
                    link={e.link}
                    authorName={e.authorName} 
                    networkId={e.networkId}
                    isIcon={e.network.icon}
                  />
                  <div class="list_category_icon cursor-pointer pt-1">
                    <NetworkIcon icon={e.network.icon} size={'font-size-h2'} />
                  </div>
                </div>
                <div onClick={() => { e.setpostInfo(e); e.handlePostpopup(true) }}  className="text-dark font-weight-normal font-size-sm overflow-hidden text-ellipsis line-clamp-2">
                  { e.content }
                </div>
              </div>
            </div>
          </div>
          <div className="mt-md-0 mt-5 list-btn">
            <div className="conn_status_wrap">
              <ConnectButton link={e.link} network={e.network.id}/>
            </div>
          </div>
        </div>
      </div>
    );
  };
  
  const SentimentPost = (props) => {
    const { loader, selectedWallFeed, isTrial } = props;

    const { negative, positive, negativeCount, positiveCount } =
      props && Object.keys(props).length ? props[1] : [];
  
    const [show, setShow] = useState(false);
    const [showPost, setshowPost] = useState(false);
    const [postInfo, setpostInfo] = useState(false);
    const [activeTab, setactiveTab] = useState("positive");
    const [postCount, setpostCount] = useState();
  
    const handlePopup = (event) => {
      setShow(event);
    };

    const handlePostpopup = (event) => {
        setshowPost(event);
      };
  
    const handleSelect = (e) => {
      setactiveTab(e)
      setpostCount(e=="positive" ? positiveCount : negativeCount)
    }

    useEffect(() => {
      setpostCount(positiveCount)
    }, [positiveCount]);
  
    return (
      <>
        <Card className="sw-card-custom gutter-b px-5 py-8 card-shadow mb-5">
          <SectionHeading isTrial={isTrial}  selectedWallFeed={selectedWallFeed} section={1} icon={'ai'} title={'Sentiment'} total={postCount} handlePopup={handlePopup} />
           <CardBody className="p-0">
            <Tabs
              activeKey={activeTab}
              onSelect={handleSelect}
              defaultActiveKey="positive"
              id="uncontrolled-tab-example"
              className="nav-tabs-line sw-custom-nav nav nav-tabs mb-3 mt-5"
            >
              <Tab
                eventKey="positive"
                title={
                  <div className="px-2 text-capitalize">
                    <img className="me-1"
                      src={baseUrl(`dashboard/analytic/smile${activeTab=='positive' ? '-active' : ''}.svg`)}
                      height={"16px"}
                      width={"16px"}
                    />{" "}
                    Positive
                  </div>
                }
              >
                {loader ? (
                  <Suspense fallback={<></>}>
                    <SentimentpostSkeleton />
                  </Suspense>
                ) : positive && Object.keys(positive).length ? (
                  positive.map((data) => {
                    return <PostData setpostInfo={setpostInfo} handlePostpopup={handlePostpopup} {...data} />;
                  })
                ) : (
                  <NoData />
                )}
              </Tab>
              <Tab
                eventKey="negative"
                title={
                  <div className="px-2">
                    <img className="me-1"
                      src={baseUrl(`dashboard/analytic/sad${activeTab=='negative' ? '-active' : ''}.svg`)}
                      height={"16px"}
                      width={"16px"}
                    />{" "}
                    Negative
                  </div>
                }
              >
                {loader ? (
                  <Suspense fallback={<></>}>
                    <SentimentpostSkeleton />
                  </Suspense>
                ) : negative && Object.keys(negative).length ? (
                  negative.map((data) => {
                    return <PostData setpostInfo={setpostInfo} handlePostpopup={handlePostpopup} {...data} />;
                  })
                ) : (
                  <NoData />
                )}
              </Tab>
            </Tabs>
          </CardBody>
        </Card>


           { postCount>5 ?     
            <PopupModal
              size={"xl"}
              showHide={show}
              handlePopup={handlePopup}
              title={ `Sentiment ${activeTab}`}>
              <Suspense fallback={<></>}>
                  <PostPopup total={postCount} moreData={activeTab=='positive' ? getwallRecordSentimentByPostStatisticsPositiveMore : getwallRecordSentimentByPostStatisticsNagativeMore} type={activeTab=="activeTab" ? 3 : 4} {...props} />
              </Suspense>
            </PopupModal> : null }
            

            <PopupModal
                size={"lg"}
                showHide={showPost}
                handlePopup={handlePostpopup}
                title={ `General Information`}
                >
                <Suspense fallback={<></>}>
                    <PostdetailPopup  {...postInfo} />
                </Suspense>
            </PopupModal>
      </>
    );
  };
  
  export default React.memo(SentimentPost);
  